.group {
  position: relative;
  margin: 5px 0;
}

.form-input {
  background: none;
  background-color: white;
  color: grey;
  height: 30px;
  font-size: 15px;
  padding: 10px 10px 10px 5px;
  display: block;
  width: 300px;
  border: none;
  border-radius: 0;
  border-bottom: 1px solid grey;
  margin: 20px 0;
}
